.login-form {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%;
  position: relative;

}

.error-container {
  position: absolute;
  top: -55px; /* Adjust this value to control the vertical position */
  left: 50%;
  transform: translateX(-50%);
  width: fit-content; /* Adjust the width as needed */
  height: 10%; /* Set the height for a square container */
  background-color: rgb(209, 104, 104);
  border-radius: 3px;
  display: none;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s;
  font-family: 'TT Commons', sans-serif;
  margin: 0 auto; /* Center the container horizontally */
  padding: 10px 20px; /* Add some padding around the content */
}

.error-container-info {
  position: absolute;
  top: -25px; /* Adjust this value to control the vertical position */
  left: 50%;
  transform: translateX(-50%);
  width: fit-content; /* Adjust the width as needed */
  height: 10%; /* Set the height for a square container */
  background-color: rgb(209, 104, 104);
  border-radius: 3px;
  display: none;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s;
  font-family: 'TT Commons', sans-serif;
  margin: 0 auto; /* Center the container horizontally */
  padding: 10px 20px; /* Add some padding around the content */
}

.error-container-info.visible {
  display: flex;
  opacity: 1;
}

.error-message {
  color: rgb(240, 240, 240);
  font-size: 15px;
  font-family: 'TT Commons', sans-serif;
}


.error-container.visible {
  display: flex;
  opacity: 1;
}

.error-message {
  color: rgb(240, 240, 240);
  font-size: 15px;
  font-family: 'TT Commons', sans-serif;
}







