@keyframes rotateRight {
  0%, 100% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
}

@keyframes rotateLeft {
  0%, 100% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(-180deg);
  }
}



.Sloth{

  margin: 0 auto;
