.remove-icon{
  position: absolute;
  height: 20px;
  top: 80%; /* Position the icon vertically in the middle */
  left: 90%; /* Position the icon horizontally in the middle */
  transform: translate(-50%, -50%); /* Center the icon perfectly */
  cursor: pointer;
  z-index: 1; /* Ensure the icon stays on top */
  background-color: #f5efdf;
  padding: 2px;
  border-radius: 50%;
