/* Budgeting.css */
.category-button {
  color: #000000;
  background-color: transparent;
  font-family: 'TT Commons', sans-serif;
  font-size: 16px;
  align-items: center;
  justify-content: center;
  border-radius: 20px; /* Adjust this value for rounded rectangular buttons */
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  display: flex;
  margin: 8px 2px;
}


.category-section {
  display: flex;
  flex-direction: column;
  align-items: left;


}

.category-label {
  width: 100%;
  text-align: left;
  margin-top: 10px;
  font-family: 'TT Commons', sans-serif;
}

.category-buttons {

  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Adjust the number of columns as needed */
  overflow: auto;
  height: 150px;
}

.category-buttons:-webkit-scrollbar-track {
  background-color: #8f0000;
}

/* Styling the scrollbar thumb (the draggable handle) */
.category-buttons::-webkit-scrollbar-thumb {
  background-color: #880000;
  border-radius: 10px;
}

/* Styling the scrollbar thumb on hover */
.category-buttons::-webkit-scrollbar-thumb:hover {
  background-color: #b60000;
}

.category-button-container {
  display: inline-block;
  position: relative;
}

.category-name {
  display: none;
  position: absolute;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 5px;
  border-radius: 4px;
  font-size: 12px; /* Adjust the font size as needed */
}

.category-button-container:hover .category-name {
  display: block;
}



