/* Media query for smaller screens */
@media (max-width: 768px) {
  .SignUp-InContainer {
    flex-direction: column; /* Stack left section on top of right section */
  }

  .LeftSection {
    flex:0.25;
    background-color: rgb(0, 0, 0); /* Keep the background color for the left section */
    border-top-right-radius: 6px;
    border-bottom-left-radius: 0px;

  }

  .RightSection {
    flex: 2.5;
    background-color: #000000; /* Maintain the background color for the right section */
    padding: 10px 20px; /* Maintain the padding */
    border-bottom-left-radius: 6px;
    border-top-right-radius: 0px;
    margin-top: 100px;
  }


  .Sloth{
      display: none;
  }


  h2{
    font-size: 22px;
  }

  .error-container {
    position: absolute;
    top: -45px; /* Adjust this value to control the vertical position */
    left: 50%;
    transform: translateX(-50%);
    width: 70%; /* Adjust the width as needed */
    height: 15%; /* Set the height for a square container */
    background-color: rgb(209, 104, 104);
    border-radius: 3px;
    display: none;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s;
    margin: 0 auto; /* Center the container horizontally */
    padding: 10px 10px; /* Add some padding around the content */

  }

  .error-message {
    font-size: 13px;
  }

  .budget-chart{
    min-width: 60%;
  }

  .CenteredContentMain {
    display: flex;
    font-size: 18px;
    overflow: hidden;
    white-space: nowrap;
    margin: 0;
    border-right: 0.15em solid rgb(238, 96, 160); /* Use your desired cursor color */
    letter-spacing: 0.15em; /* Adjust spacing as needed */
    animation: typing 2s steps(20, end), blink-caret 0.5s step-end 2s infinite;
    
  }

}

